<template>
  <div class="shooter">
  	<!-- <h3>中超-射手榜</h3> -->
  	<dl>
  		<dt>
  		    <div class="type3">排名</div>
  			<div class="type1">球员</div>
  			<div class="type1">球队</div>
  			<div class="type3">进球数</div>
  		</dt>
  		<dd v-for="(item, index) in shooter">
  			<div class="type3" v-text="index+1"></div>
  			<div class="type1" v-text="item.name"></div>
  			<div class="type1">
  				<img :src="'//img.dongqiudi.com/data/pic/'+item.team_id+'.png'"/>
  				<span v-html="item.team_name"></span>
  			</div>
  			<div class="type3" v-text="item.count"></div>
  		</dd>
  	</dl>
  </div>
</template>

<script>
  export default {
  	props: {
  	  shooter: {
  		type: Array,
  		required: true
  	  }
  	},
  	mounted() {
      setTimeout(() => {
      	// console.log(this.shooter[0])
      	this.$emit('updateScroll')
      },20)
  	}
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.shooter
  h3
  	text-align: left
  p
  	text-align: left
  dl
  	background: #e2e2e2
  	font-siez: 1rem
	dt
	  display: flex
	  padding: .6rem 0
	  .type1
	  	flex: 4
	  	text-align: center
	  .type3
	  	flex: 2
	dd
	  display: flex
	  border-bottom: 1px solid #ddd
	  padding: 1rem 0
	  font-size: 12px
	  position: relative
	  .type1
	  	flex: 4
	  	text-align: center
	  	img
	  	  width: 20px
	  	  position: absolute
	  	  top: .8rem
	  	  left: 3rem
	  .type3
	  	flex: 2
</style>



